
.action-panel {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.apt-card-body {
  flex: 1;
  overflow-y: auto;
}

.action-section {
  margin-bottom: 24px;

  &:last-child {
    margin-bottom: 0;
  }
}

.section-title {
  font-size: 0.95em;
  font-weight: 600;
  color: var(--apt-primary);
  margin-bottom: 12px;
  display: flex;
  align-items: center;
  gap: 6px;
}

.button-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
}

.apt-btn {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4px;
  padding: 12px 8px;
  font-size: 0.85em;

  .btn-icon {
    font-size: 1.3em;
  }
}

.apt-btn-small {
  padding: 6px 12px;
  font-size: 0.8em;
}

.room-select-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 8px;
  max-height: 300px;
  overflow-y: auto;
}

.room-btn {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4px;
  padding: 10px 8px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid var(--apt-border);
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s;
  color: var(--apt-text);
  font-size: 0.8em;

  &:hover:not(:disabled) {
    background: rgba(255, 107, 157, 0.15);
    border-color: var(--apt-primary);
    transform: translateY(-2px);
  }

  &.active {
    background: rgba(255, 107, 157, 0.2);
    border-color: var(--apt-primary);
    box-shadow: 0 0 8px rgba(255, 107, 157, 0.3);
  }

  &:disabled {
    opacity: 0.6;
    cursor: not-allowed;
  }

  .room-icon {
    font-size: 1.5em;
  }

  .room-label {
    font-weight: 600;
    font-size: 0.95em;
  }

  .room-type-label {
    font-size: 0.75em;
    opacity: 0.8;
  }
}

.tenant-interaction {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.tenant-interact-card {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 12px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid var(--apt-border);
  border-radius: 8px;
  transition: all 0.2s;

  &:hover {
    background: rgba(255, 107, 157, 0.1);
    border-color: var(--apt-primary);
  }
}

.tenant-interact-info {
  display: flex;
  align-items: center;
  gap: 8px;

  .tenant-icon {
    font-size: 1.3em;
  }

  .tenant-name {
    font-weight: 600;
    color: var(--apt-text);
  }
}
