
.map-view {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.apt-card-body {
  flex: 1;
  overflow-y: auto;
}

.floor-level {
  background: var(--apt-card);
  border: 1px solid var(--apt-border);
  border-radius: 12px;
  padding: 16px;
  margin-bottom: 16px;

  &:last-child {
    margin-bottom: 0;
  }
}

.floor-title {
  font-size: 1.1em;
  font-weight: 600;
  margin-bottom: 12px;
  color: var(--apt-text);
  display: flex;
  align-items: center;
  gap: 8px;

  &::before {
    content: 'ðŸ¢';
  }
}

.floor-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 12px;
  width: 100%;
}

.room-card {
  border-radius: 10px;
  padding: 12px 8px;
  min-height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-weight: 600;
  font-size: 0.85em;
  border: 2px solid rgba(255, 255, 255, 0.3);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
  cursor: pointer;
  transition: all 0.2s;
  position: relative;

  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 6px 16px rgba(255, 107, 157, 0.3);
    border-color: var(--apt-primary);
  }

  // æˆ¿é—´ç±»åž‹æ ·å¼
  &.bedroom {
    background: var(--room-bedroom);
  }

  &.bedroom.vacant {
    background: var(--room-bedroom-vacant);
    border-color: rgba(255, 182, 193, 0.8);
  }

  &.empty {
    background: var(--room-empty);
    border-color: #5a5f6b;
    opacity: 0.7;
  }

  &.your {
    background: var(--room-player);
    border-color: #5dade2;
  }

  &.public-lounge {
    background: var(--room-lounge);
    border-color: #fbbf24;
  }

  &.public-gym {
    background: var(--room-gym);
    border-color: #34d399;
  }

  &.public-laundry {
    background: var(--room-laundry);
    border-color: #38bdf8;
  }

  &.custom-functional {
    background: var(--room-custom);
    border-color: rgba(52, 211, 153, 0.8);
  }
}

.room-name {
  font-weight: bold;
  font-size: 1em;
  margin-bottom: 4px;
}

.room-type {
  font-size: 0.75em;
  opacity: 0.9;
  margin-bottom: 2px;
}

.room-occupant {
  font-size: 0.7em;
  color: var(--apt-dim);
  margin-top: 2px;
}

.room-people {
  margin-top: 8px;
  padding-top: 8px;
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  width: 100%;
}

.person-indicator {
  font-size: 0.7em;
  margin: 2px 0;
  padding: 2px 4px;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 4px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
