@use './styles/theme.scss';

#game-container {
  width: 100%;
  min-height: 600px;
  background: var(--apt-bg);
  font-family: 'Microsoft YaHei', 'Segoe UI', sans-serif;
  color: var(--apt-text);
}

.game-layout {
  display: flex;
  width: 100%;
  height: 600px;
  gap: 16px;
  padding: 16px;
}

.left-panel {
  width: 280px;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  gap: 16px;

  > * {
    flex: 1;
  }
}

.center-panel {
  flex: 1;
  min-width: 0;
}

.right-panel {
  width: 320px;
  flex-shrink: 0;
}

// å“åº”å¼å¸ƒå±€
@media (max-width: 1200px) {
  .game-layout {
    flex-direction: column;
    height: auto;
  }

  .left-panel,
  .center-panel,
  .right-panel {
    width: 100%;
    min-height: 400px;
  }

  .left-panel {
    > * {
      min-height: 300px;
    }
  }
}
