
.status-bar {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.apt-card-body {
  flex: 1;
  overflow-y: auto;
}

.status-section {
  margin-bottom: 20px;
  padding-bottom: 16px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);

  &:last-child {
    border-bottom: none;
    margin-bottom: 0;
  }
}

.section-title {
  font-size: 0.95em;
  font-weight: 600;
  color: var(--apt-primary);
  margin-bottom: 12px;
  display: flex;
  align-items: center;
  gap: 6px;
}

.subsection-title {
  font-size: 0.85em;
  color: var(--apt-dim);
  margin-bottom: 8px;
  margin-top: 8px;
}

.time-display {
  text-align: center;
  padding: 12px;
  background: rgba(255, 107, 157, 0.1);
  border-radius: 8px;
}

.time-large {
  font-size: 2em;
  font-weight: 700;
  color: var(--apt-primary);
  margin-bottom: 4px;
  font-family: 'Courier New', monospace;
}

.date-info {
  font-size: 0.9em;
  color: var(--apt-text);
  opacity: 0.9;
}

.info-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 0;
  font-size: 0.9em;

  .label {
    color: var(--apt-dim);
  }

  .value {
    color: var(--apt-text);
    font-weight: 500;

    &.highlight {
      color: var(--apt-primary);
      font-weight: 700;
      font-size: 1.1em;
    }
  }
}

.info-text {
  font-size: 0.85em;
  color: var(--apt-dim);
  font-style: italic;
  text-align: center;
  padding: 8px;
}

.nearby-tenants {
  margin-top: 8px;
}

.tenant-tag {
  display: inline-block;
  padding: 4px 10px;
  margin: 4px 4px 4px 0;
  background: rgba(255, 107, 157, 0.2);
  border: 1px solid var(--apt-border);
  border-radius: 12px;
  font-size: 0.85em;
  color: var(--apt-text);
}
