:root {
  // ä¸»è‰²è°ƒ
  --apt-primary: #ff6b9d;
  --apt-secondary: #c44569;

  // èƒŒæ™¯è‰²
  --apt-bg: #1a1a2e;
  --apt-card: #16213e;
  --apt-border: rgba(255, 107, 157, 0.2);

  // æ–‡å­—è‰²
  --apt-text: #e8eaf6;
  --apt-dim: #e5e7eb;

  // æˆ¿é—´ç±»åž‹é¢œè‰²
  --room-bedroom: linear-gradient(135deg, #592e39 0%, #7a4053 100%);
  --room-bedroom-vacant: linear-gradient(135deg, rgba(255, 182, 193, 0.6) 0%, rgba(255, 192, 203, 0.7) 100%);
  --room-empty: linear-gradient(135deg, #3d3f47 0%, #4a4f5a 100%);
  --room-player: linear-gradient(135deg, #2e5266 0%, #3a6d8c 100%);
  --room-lounge: linear-gradient(135deg, #d97706 0%, #f59e0b 100%);
  --room-gym: linear-gradient(135deg, #059669 0%, #10b981 100%);
  --room-laundry: linear-gradient(135deg, #0284c7 0%, #0ea5e9 100%);
  --room-custom: linear-gradient(135deg, rgba(16, 185, 129, 0.6) 0%, rgba(52, 211, 153, 0.7) 100%);
}

// å…¨å±€é‡ç½®æ ·å¼ï¼ˆä»…é’ˆå¯¹æ¸¸æˆå®¹å™¨ï¼‰
#game-container {
  * {
    box-sizing: border-box;
  }
}

// æ»šåŠ¨æ¡æ ·å¼
.custom-scrollbar {
  &::-webkit-scrollbar {
    width: 8px;
  }

  &::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.05);
  }

  &::-webkit-scrollbar-thumb {
    background: var(--apt-primary);
    border-radius: 4px;
  }
}

// é€šç”¨å¡ç‰‡æ ·å¼
.apt-card {
  background: var(--apt-card);
  border: 1px solid var(--apt-border);
  border-radius: 10px;
  overflow: hidden;
}

.apt-card-title {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 16px;
  background: rgba(255, 107, 157, 0.15);
  font-weight: 600;
  font-size: 16px;
  color: var(--apt-text);
}

.apt-card-body {
  padding: 16px;
}

// æŒ‰é’®æ ·å¼
.apt-btn {
  padding: 10px 20px;
  background: linear-gradient(135deg, var(--apt-primary) 0%, var(--apt-secondary) 100%);
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 600;
  transition: all 0.2s;
  box-shadow: 0 2px 8px rgba(255, 107, 157, 0.3);

  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 4px 12px rgba(255, 107, 157, 0.5);
  }

  &:active {
    transform: translateY(0);
  }

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
    transform: none;
  }
}

.apt-btn-secondary {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid var(--apt-border);

  &:hover {
    background: rgba(255, 255, 255, 0.15);
  }
}
